<template>
  <div class="demo-model">
    <el-carousel
      @change="handleCardChange"
      :autoplay="false"
      trigger="click"
      type="card"
      height="100px"
    >
      <el-carousel-item v-for="(item,index) in algArr" :key="index">
        <h3>{{item}}</h3>
      </el-carousel-item>
    </el-carousel>

    <template v-if="curAlg==''">
      <div><span style="font-weight: bold"></span>
        <span style="background:#fb5a52;color:white"></span>
        <span style="background:#32b900;color:white"></span>
      </div>
      
      <h4></h4>
      <p></p>
    </template>
    <template v-if="curAlg==''">
      <h4></h4>
      <p></p>
    </template>
    <template v-if="curAlg==''">
      <h4></h4>
      <p></p>
    </template>
    curAlg in store: {{ curAlg }}
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "demo-model",
  data() {
    return {};
  },
  computed: {
    ...mapState("demo", ["algArr", "curAlg"])
  },
  methods: {
    ...mapMutations("demo", ["changeCurAlg"]),
    handleCardChange(cur) {
      // (cur, old)  
      this.changeCurAlg(this.algArr[cur]);
      console.log("%cthis.curAlg:", "color:blue", this.curAlg);
      // console.log("cur:", cur, this.algArr[cur]);
      // console.log("old:", old, this.algArr[old]);
    }
  }
};
</script>

